/********** Template CSS **********/
:root {
    --primary: #00B074;
    --secondary: #2B9BFF;
    --light: #EFFDF5;
    --dark: #2B3940;
}
.back-to-top {
    position: fixed;
    display: none;
    right: 45px;
    bottom: 45px;
    z-index: 99;
}
/*** Spinner ***/
#spinner {
    opacity: 0;
    visibility: hidden;
    -webkit-transition: opacity .5s ease-out, visibility 0s linear .5s;
    transition: opacity .5s ease-out, visibility 0s linear .5s;
    z-index: 99999;
}
#spinner.show {
    -webkit-transition: opacity .5s ease-out, visibility 0s linear 0s;
    transition: opacity .5s ease-out, visibility 0s linear 0s;
    visibility: visible;
    opacity: 1;
}
/*** Button ***/
.btn {
    font-family: 'Inter', sans-serif;
    font-weight: 600;
    -webkit-transition: .5s;
    transition: .5s;
}
.btn.btn-primary,
.btn.btn-secondary {
    color: #FFFFFF;
}
.btn-square {
    width: 38px;
    height: 38px;
}
.btn-sm-square {
    width: 32px;
    height: 32px;
}
.btn-lg-square {
    width: 48px;
    height: 48px;
}
.btn-square,
.btn-sm-square,
.btn-lg-square {
    padding: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    font-weight: normal;
    border-radius: 2px;
}
/*** Navbar ***/
.navbar .dropdown-toggle::after {
    border: none;
    content: "\F107";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    vertical-align: middle;
    margin-left: 5px;
    -webkit-transition: .5s;
    transition: .5s;
}
.navbar .dropdown-toggle[aria-expanded=true]::after {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}
.navbar-dark .navbar-nav .nav-link,
.navbar-light .navbar-nav .nav-link {
    margin-right: 30px;
    padding: 25px 0;
    color: #FFFFFF;
    font-size: 15px;
    text-transform: uppercase;
    outline: none;
}
.navbar-dark .navbar-nav .nav-link:hover,
.navbar-dark .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .nav-link:hover,
.navbar-light .navbar-nav .nav-link.active {
    color: var(--primary);
}
@media (max-width: 991.98px) {
    .navbar-dark .navbar-nav .nav-link,
    .navbar-light .navbar-nav .nav-link  {
        margin-right: 0;
        padding: 10px 0;
    }

    .navbar-light .navbar-nav {
        border-top: 1px solid #EEEEEE;
    }
}
.navbar-light .navbar-brand,
.navbar-light a.btn {
    height: 75px;
}
.navbar-light .navbar-nav .nav-link {
    color: var(--dark);
    font-weight: 500;
}
.navbar-light.sticky-top {
    top: -100px;
    -webkit-transition: .5s;
    transition: .5s;
}
@media (min-width: 992px) {
    .navbar .nav-item .dropdown-menu {
        display: block;
        top: 100%;
        margin-top: 0;
        -webkit-transform: rotateX(-75deg);
                transform: rotateX(-75deg);
        -webkit-transform-origin: 0% 0%;
                transform-origin: 0% 0%;
        opacity: 0;
        visibility: hidden;
        -webkit-transition: .5s;
        transition: .5s;
        
    }

    .navbar .nav-item:hover .dropdown-menu {
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
        visibility: visible;
        -webkit-transition: .5s;
        transition: .5s;
        opacity: 1;
    }
}
/*** Header ***/
.header-carousel .container,
.page-header .container {
    position: relative;
    padding: 45px 0 45px 35px;
    border-left: 15px solid var(--primary);
}
.header-carousel .container::before,
.header-carousel .container::after,
.page-header .container::before,
.page-header .container::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100px;
    height: 15px;
    background: var(--primary);
}
.header-carousel .container::after,
.page-header .container::after {
    top: 100%;
    margin-top: -15px;
}
@media (max-width: 768px) {
    .header-carousel .owl-carousel-item {
        position: relative;
        min-height: 500px;
    }
    
    .header-carousel .owl-carousel-item img {
        position: absolute;
        width: 100%;
        height: 100%;
        -o-object-fit: cover;
           object-fit: cover;
    }

    .header-carousel .owl-carousel-item p {
        font-size: 14px !important;
        font-weight: 400 !important;
    }

    .header-carousel .owl-carousel-item h1 {
        font-size: 30px;
        font-weight: 600;
    }
}
.header-carousel .owl-nav {
    position: absolute;
    top: 50%;
    right: 8%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.header-carousel .owl-nav .owl-prev,
.header-carousel .owl-nav .owl-next {
    margin: 7px 0;
    width: 45px;
    height: 45px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    color: #FFFFFF;
    background: transparent;
    border: 1px solid #FFFFFF;
    border-radius: 2px;
    font-size: 22px;
    -webkit-transition: .5s;
    transition: .5s;
}
.header-carousel .owl-nav .owl-prev:hover,
.header-carousel .owl-nav .owl-next:hover {
    background: var(--primary);
    border-color: var(--primary);
}
.page-header {
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(43, 57, 64, .5)), to(rgba(43, 57, 64, .5))), url(/static/img/carousel-1.f9d9e1b.jpg) center center no-repeat;
    background: linear-gradient(rgba(43, 57, 64, .5), rgba(43, 57, 64, .5)), url(/static/img/carousel-1.f9d9e1b.jpg) center center no-repeat;
    background-size: cover;
}
.breadcrumb-item + .breadcrumb-item::before {
    color: var(--light);
}
/*** About ***/
.about-bg {
    background-image: -webkit-repeating-radial-gradient(center center, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) 1px, transparent 1px, transparent 100%);
    background-image: repeating-radial-gradient(center center, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) 1px, transparent 1px, transparent 100%);
    background-size: 5px 5px;
}
/*** Category ***/
.cat-item {
    display: block;
    -webkit-box-shadow: 0 0 45px rgba(0, 0, 0, .08);
            box-shadow: 0 0 45px rgba(0, 0, 0, .08);
    border: 1px solid transparent;
    -webkit-transition: .5s;
    transition: .5s;
}
.cat-item:hover {
    border-color: rgba(0, 0, 0, .08);
    -webkit-box-shadow: none;
            box-shadow: none;
}
/*** Job Listing ***/
.nav-pills .nav-item .active {
    border-bottom: 2px solid var(--primary);
}
.job-item {
    border: 1px solid transparent;
    border-radius: 2px;
    -webkit-box-shadow: 0 0 45px rgba(0, 0, 0, .08);
            box-shadow: 0 0 45px rgba(0, 0, 0, .08);
    -webkit-transition: .5s;
    transition: .5s;
}
.job-item:hover {
    border-color: rgba(0, 0, 0, .08);
    -webkit-box-shadow: none;
            box-shadow: none;
}
/*** Testimonial ***/
.testimonial-carousel .owl-item .testimonial-item,
.testimonial-carousel .owl-item.center .testimonial-item * {
    -webkit-transition: .5s;
    transition: .5s;
}
.testimonial-carousel .owl-item.center .testimonial-item {
    background: var(--primary) !important;
}
.testimonial-carousel .owl-item.center .testimonial-item * {
    color: #FFFFFF !important;
}
.testimonial-carousel .owl-dots {
    margin-top: 24px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: end;
        -ms-flex-align: end;
            align-items: flex-end;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
